/*! Primitive
  by Tania Rascia */

// Configuration
@import "base/variables";
@import "base/mixins";

// Reset
@import "base/normalize";
@import "base/reset";

// Components
@import "components/scaffolding";
@import "components/grid";
@import "components/helpers";
@import "components/buttons";
@import "components/forms";
@import "components/tables";
@import "components/navigation";
@import "components/layout";

html {
	height: 100%;
}

body {
	min-height: 100%;
}

/* Header Portion */

header {
	margin-top: 0;
	height: $header-height;
	background-color: $primary-color;
	box-shadow: 0 2px 5px rgba(0,0,0,.3);

	.max-width {
		max-width: $large;
	}
}

.header-logo {
	height: $header-height;

	.uw-patch {
		margin-left: 2em;
		display: block;
	    text-indent: -9999px;
	    width: 163px;
	    height: 90px;
	    background: url(assets/svg/uw-sprite.svg) no-repeat 0 0 transparent;
	    top: -22px;
	    left: 0;
	    z-index: 1;
	    transition: 0.25s;
	    &:hover {
	    	background: url(assets/svg/uw-sprite-focus.svg) no-repeat 0 0 transparent;
	    }
	}

	.uw-wordmark {
		margin-top:  - calc((#{$header-height} + 24) / 2);
		margin-left: 163px;
		display: block;
	    top: calc((#{$header-height} - 24px) / 2);
	    left: 150px;
	    text-indent: -9999px;
	    width: 330px;
	    height: 24px;
	    background: url(assets/svg/uw-sprite.svg) no-repeat 0 -431px transparent;
	}

}

nav {
	margin-top: -$header-height; 
	display: block;
	vertical-align: middle;
	overflow-x: hidden;
	width: 500px;
	float: right;

	ul {
		height: $header-height;
		margin: 0;
		overflow: hidden;
		text-align: right;
	}

	li {
		display: inline;
		list-style: none;
		height: 44px;

		a {
			display: inline-block;
			color: white;
			font-size: 16px;
			font-weight: 500;
			padding: 10px 25px 10px 0;
			margin-top: calc((#{$header-height} - 44) / 2);
			transition: 0.25s;

			&:hover {
				color: $accent-color;
				text-decoration: none;
			}
		}
	}

	@include small-max-breakpoint {
		float: right;
		display: none;
	}
}

/* Hero Portion */

.hero {
	height: $hero-height;
	width: 100%; 
	&::before {
		content: "";
  		position: absolute;
		height: $hero-height;
		width: 100%;
		background: -webkit-gradient(linear,0 0,0 100%,from(transparent),to(rgba(0,0,0,.5))) 0 0 no-repeat, url(https://lederer.stat.washington.edu/wp-content/uploads/2015/12/Formula.jpg);
		background-repeat: no-repeat;
		background-position: center; 
		background-size: contain;
		opacity: 0.3;
		top: $header-height;
	    left: 0;
	    bottom: 0;
	    right: 0;
	    z-index: -1;   
	}

	#hero-data {
		height: 100%; 
		max-width: $large;
	}
}

#hero-data p {
	padding-left: 15px;
	color: $primary-color;
	font-size: 45px;
	content: "";
    display: inline-block;
	height: $hero-height;
	vertical-align: middle;
	font-family: "Encode Sans Compressed",sans-serif;
	text-shadow: 1px 1px 2px rgba(0,0,0,.5);
	font-weight: 900;
	opacity: 1;
	transition: 0.25s;

	@include small-max-breakpoint {
		font-size: 30px;
		padding-top: 7.5px;
	}
}

/* Main Portion */ 

.main-content {
	padding-bottom: 25px;
	h1 {
		color: $primary-color;
		font-size: 37px;
		margin-top: 29px;
		margin-bottom: 14.5px;
		font-family: "Encode Sans Compressed", sans-serif;
	    font-weight: 800;
	    line-height: 1.3;
	}

	h2 {
		font-size: 22px;
		font-weight: 900;
		color: $primary-color;
		margin-top: 58px;
		margin-bottom: 10px;
	}

	.widget-bar {
		width: 100px;
		background-color: $accent-color;
	    content: "";
	    height: 4px;
	    &::after {
	    	transform: skew(-25deg,0);
		    left: 80px;
		    background-color: #fff;
		    height: 8px;
	    }
	}

	.application {
		width: 100%;
		height: 1000px;
		border: 0;
	}
}

.posts-data {
    ul {
    	padding-left: 20px;
    	list-style-type: disc;
    	font-family: 'Open Sans',sans-serif;
    	font-size: 17px;
    	color: #595959;
    }

    li {
    	color: #08d;
	    text-decoration: none;
	    transition: .25s;
    }
}


/* Footer Portion */ 

.footer {
	bottom: 0;
	height: $footer-height;
	background: url(assets/images/footer.jpg) center bottom;
    background-size: cover;
    padding: 0 0 30px 0;
    background-color: $primary-color;
    border-top: 7px solid #d1d1d1;
    text-align: center;
    position: relative;

    .footer-data {
    	max-width: $large;
    
	    .footer-wordmark {
	    	display: inline-block;
		    width: 335px;
		    background: url(assets/svg/uw-sprite.svg) no-repeat 0 -434px transparent;
		    text-indent: -9999px;
		    overflow: hidden;
		    margin-top: 50px;
		    transition: 0.25s;
		    &:hover {
		    	color: #b7a57a;
		    	background: url(assets/svg/uw-sprite-focus.svg) no-repeat 0 -434px transparent;
		    }
	    }

	    h3 {
	    	margin-top: 0.1em;
	    }

	    .statistics {
	    	color: white;
	    	font-family: "Encode Sans Compressed";
    		font-weight: 300;
    		transition: 0.25s;
    		&:hover {
	    		color: $accent-color;
	    		text-decoration: none;
	    	}
	    }

	    .footer-information {
	    	margin-top: -0.5em;
	    	font-family: "Open Sans", sans-serif;
	    	color: white;
	    }
    }
}

/* Fix for Margins on Large Screens */

#hero-image, #hero-data, .max-width, .footer-data{
	margin: 0 auto;
}